/*==============================================================
# Buttons
==============================================================*/

.button {
    font-weight: 600;
    font-size: 14px;
    letter-spacing: .5px;
    line-height: 20px;
    text-transform: uppercase;
    border-radius: 3px;
    display: inline-block;
    cursor: pointer;
    text-align: center;
    margin: 5px;
    padding: 12px 40px;
    appearance: none;
    user-select: none;
    transition: background-color .3s;
    &.gray-color {
    	background-color: $gray;
    	color: $white;
    }
    .button-text,
    .button-icon {
        display: inline-block;
        vertical-align: middle;
    }
    .button-icon{
        width: 18px;
        height: 20px;
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 0 5px;
        position: relative;
        transition: .3s $cbTrans all;
        svg {
            float: left;
            width: 100%;
            height: 100%;
        }
    }
    &.active-button {
        .button-icon {
            transform: rotate(-180deg);
        }
    }
    &.gray-color {
        svg {
            fill: $white;
        }
    }
}

a {
    .link-text,
    .link-icon {
        display: inline-block;
        vertical-align: middle;
    }
    .link-text {
        text-transform: uppercase;
        font-size: 14px;
        line-height: 100%;
    }
    .link-icon {
        height: 25px;
        width: 25px;
        border-radius: 100%;
        padding: 3px;
        svg {
            float: left;
            width: 100%;
            height: 100%;
        }
    }
}